.team {
  padding-top: 49px;
  padding-bottom: 59px;
  margin: 0 auto;
}
.team__img {
  width: 100%;
}

.team__subtitle {
  color: var(--dark);
  font-family: inherit;
  font-size: 18px;
  line-height: 1.67;
  text-align: center;
  margin-bottom: 15px;

  @media screen and (min-width: 768px) {
    font-size: 20px;
    line-height: 1.65;
    margin-bottom: 16px;
  }
}

.team__title {
  color: var(--dark);
  font-family: inherit;
  font-weight: 300;
  font-size: 38px;
  line-height: 1.26;
  text-align: center;
  margin: auto;
  max-width: 220px;
  margin-bottom: 34px;

  @media screen and (min-width: 768px) {
    max-width: 410px;
    margin-bottom: 35px;
  }
}

.team__list {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 10px;

  @media screen and (min-width: 768px) {
    display: flex;
    flex-direction: unset;
    justify-content: center;
    align-items: center;
    gap: 19px;
  }
}

.team__text {
  width: 100%;
  color: var(--dark);
  font-family: inherit;
  font-size: 16px;
  line-height: 1.63;
  margin: auto;
  text-align: center;
  margin-bottom: 37px;

  @media screen and (max-width: 767px) {
    max-width: 207px;
  }

  @media screen and (min-width: 768px) {
    width: 625px;
    font-size: 17px;
    line-height: 1.64;
    margin-bottom: 49px;
  }

  @media screen and (min-width: 1360px) {
    margin: auto;
    width: 648px;
    font-size: 18px;
    line-height: 1.53;
    margin-bottom: 45px;
  }
}

.team__item {
  cursor: pointer;

  &:hover .team__social,
  &:hover .team__card::after {
    opacity: 1;
  }
}

.team__card {
  width: 100%;

  position: relative;
  margin-bottom: 15px;

  @media screen and (max-width: 767px) {
    max-width: 280px;
  }

  @media screen and (min-width: 768px) {
    margin-bottom: 14px;
    width: 223px;
  }

  @media screen and (min-width: 1360px) {
    margin-bottom: 11px;
    width: 420px;
  }

  &::after {
    content: '';
    position: absolute;
    top: 0%;
    left: 0%;
    width: 100%;
    height: 100%;
    opacity: 0;
    background: rgba(47, 48, 58, 0.4);
    transition: opacity 250ms linear;
  }
}

.team__icon {
  fill: var(--main);
  transition: fill 250ms linear;

  &:hover {
    fill: var(--accent);
    stroke: rgba(47, 48, 58, 0.4);
  }
}

.team__social {
  display: flex;
  gap: 25px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  opacity: 0;
  z-index: 1;
}

.team__name {
  color: var(--dark);
  font-family: inherit;
  font-weight: 400;
  font-size: 23px;
  line-height: 1.26;
  text-align: center;
  margin-bottom: 11px;

  @media screen and (min-width: 768px) {
    font-size: 25px;
    line-height: 1.28;
    margin-bottom: 12px;
  }
}

.team__description {
  color: var(--dark);
  font-family: inherit;
  font-size: 16px;
  line-height: 1.63;
  text-align: center;

  @media screen and (min-width: 768px) {
    font-size: 18px;
    line-height: 1.53;
  }
}
